import React, { useReducer,useEffect, useState } from 'react'
import {
  TheContent,
  TheInitSidebar,
  TheFooter,
  TheInitHeader
} from './index'


import appReducer from '../appstate/reducers';
import api from '../appstate/api'
let isMounted;

const useResquest = (dispatch) => {
  const [value, setValue] = useState(null)
  const [msg, setMsg] = useState(null)

  useEffect(() => {
    isMounted = true;
    var fm = new FormData();
    fm.append('s', '');fm.append('m', document.cookie.split("=")[1]);
    dispatch({type:'LOADING', initloading: true})
    
    if (document.cookie.split("=")[1] === undefined) {
      api.fxns.base(fm,api.fxns.endpoint).then(dd => {
        if(isMounted){
          document.cookie = "_inspire=" + dd["PHPSESSID"];
          setValue(dd["PHPSESSID"])
          dispatch({type:'LOADING', initloading: false,cnt:dd['PHPSESSID']})
        }
      }, err=>{

        setValue(false)
        setMsg(err.toString()+' Application Start')
        dispatch({type:'LOADING', initloading: false})
      });
    }else {
      var fm = new FormData();
      fm.append('s', '');fm.append('m', 'cn');
      api.fxns.base(fm,api.fxns.endpoint).then(dd => {
          setValue(true)
          dispatch({type:'LOADING', initloading: false,cnt:dd['sd']})

      }, err=>{

        setValue(false)
        setMsg(err.toString()+' Application Start')
        dispatch({type:'LOADING', initloading: false})
      });
      
      }

      return () => {
        isMounted = false
      };
  },[])

  return {value,msg};
}

const TheInitLayout = () => {
  const [state, dispatch] = useReducer(appReducer,{menu:[],auhmn:[]})
  const rd = useResquest(dispatch)
  const { value,msg } = rd;

  

  return (
    <div className="c-app c-default-layout">
      <TheInitSidebar/>
      <div className="c-wrapper">
        <TheInitHeader/>
        <div className="c-body">
          <TheContent/>
        </div>
        <TheFooter/>
      </div>
    </div>
  )
}

export default TheInitLayout
